

.navigation{ 
  height: 60px;
  display: flex;
  h1{
    display: flex;
    font-family: 'Signika Negative', sans-serif;
    color: white;
    font-size: 2.2rem;
    text-shadow: 1px -1px 20px #0e1427;
    align-items: center;
    justify-content: center;
    margin-left:400px;
  }
  ul{
    display: flex;
    justify-content:left;
    align-items: center;
    height: 60px;
    a{
      text-decoration: none;
      color: white;
    }
    li .active{
      background:rgb(22, 88, 155);
      text-decoration: none;
      list-style-type: none;
      margin:20px 10px;
      padding: 8px;
      border-radius:20px;
    }
    li{
      text-decoration: none;
      list-style-type: none;
      margin:20px 10px;
      padding: 8px;
      border-radius:20px;
    }
  
  }
}
