

h4{
  font-family: 'Signika Negative', sans-serif;
  text-align: center;
  color: white;
}
ul{
  display: flex;
}

.categorie{
  background:rgb(17, 31, 44);
  border-radius: 25px;
  outline: none;
  color: white;
  font-family: 'Signika Negative', sans-serif;
  text-align: center;
  padding: 8px;
  border: 1px solid rgb(38, 58, 74);
  margin: 5px;
}
.add{
  padding: 10px;
  color: white;
  font-family: 'Signika Negative', sans-serif;
  text-align: center;
  background: rgb(13, 58, 97);
  z-index: 1000;
  border-radius: 50px;
  display: block;
  margin: 0 auto;
  border: 1px solid transparent;
  position: absolute;
  bottom: 10px;
  &:hover{
    transition: 0.3s ease;
    transform: scale(0.9);
    cursor: pointer;
  }
}
i{
  font-size: 1.1rem;
  color: rgb(227, 168, 57);
}
.note{
  font-family: 'Signika Negative', sans-serif;
  color: white;
 margin-left:20px;

}
.date{
  color: rgba(71, 69, 69, 0.801);
  font-weight: lighter;
  font-family: 'Signika Negative', sans-serif;
  text-align: center;
}
.message{
  font-family: 'Signika Negative', sans-serif;
  text-align: center;
}
.card{
  position: relative;
  padding: 10px;
  height: 500px;
  background: rgb(30, 23, 56);;
  margin: 10px;
  border-radius: 25px;
  overflow: hidden;
  img{
    width: 90%;
    display: block;
    margin: 10px auto;
    height:50%;
    border-radius:25%;
  }
}