
.loader{
  display: block;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list{
  width: 70%;
  display:grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(4,1fr);
  margin: 0 auto;
}
input[type="text"]{
  display: block;
  border-radius: 10px 10px 0 0;
  outline: none;
  &::placeholder{
      color: black;
      font-family: 'Signika Negative', sans-serif;
      text-align: center;
  }
}
input[type="button"]{
  display: block;
  border-radius: 0 0 10px 10px;
  background: rgb(84, 132, 234);
  color: white;
  font-size: 1rem;
  
}
.form-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  input{
    display: block;
    padding: 7px;
    width: 300px;

  }
}